 <div class="text-center min-h-screen relative mx-auto sm:max-w-xl md:max-w-full  mt-8 mb-8" style="max-width:80%"> 

<ol class="flex items-center w-full text-sm font-medium text-center text-gray-500 dark:text-gray-400 sm:text-base">
    <li class="flex md:w-full items-center  dark:text-blue-500 sm:after:content-[''] after:w-full after:h-1 after:border-b after:border-gray-200 after:border-1 after:hidden sm:after:inline-block after:mx-6 xl:after:mx-10 dark:after:border-gray-700">
        <span v-if="$store.state.core.banner.step > 1" class="flex items-center after:content-['/'] sm:after:hidden after:mx-2 after:text-gray-200 dark:after:text-gray-500">
            <svg  aria-hidden="true" class="w-4 h-4 mr-2 sm:w-5 sm:h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clip-rule="evenodd"></path></svg>
            Choose <span class="hidden sm:inline-flex sm:ml-2">Format</span>
        </span>
         <span v-else class="flex items-center after:content-['/'] sm:after:hidden after:mx-2 after:text-gray-200 dark:after:text-gray-500">
            <span class="mr-2">1</span>
            Choose <span class="hidden sm:inline-flex sm:ml-2">Format</span>
        </span>
        
    </li>
    <li class="flex md:w-full items-center after:content-[''] after:w-full after:h-1 after:border-b after:border-gray-200 after:border-1 after:hidden sm:after:inline-block after:mx-6 xl:after:mx-10 dark:after:border-gray-700">
        <span v-if="$store.state.core.banner.step > 2" class="flex items-center after:content-['/'] sm:after:hidden after:mx-2 after:text-gray-200 dark:after:text-gray-500">
            <svg  aria-hidden="true" class="w-4 h-4 mr-2 sm:w-5 sm:h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clip-rule="evenodd"></path></svg>
            Select <span class="hidden sm:inline-flex sm:ml-2">Dimensions</span>
        </span>
        <span v-else class="flex items-center after:content-['/'] sm:after:hidden after:mx-2 after:text-gray-200 dark:after:text-gray-500">
            <span class="mr-2">2</span>
            Select <span class="hidden sm:inline-flex sm:ml-2">Dimensions</span>
        </span>
    </li>
    <li class="flex items-center">
        <span class="mr-2">3</span>
        Customize
    </li>
</ol>
  <ChooseFormat
    v-if="$store.state.core.banner.step == 1"
  />
  <SelectDimensions
    v-if="$store.state.core.banner.step == 2"
  />
<CustomizeBanner
    v-if="$store.state.core.banner.step == 3"

/>

<div class="inline-flex rounded-md shadow-sm">
  <a v-if="$store.state.core.banner.step > 1" @click.prevent="setStep(-1)" href="#" aria-current="page" class="px-4 py-2 text-sm font-medium text-blue-700 bg-white border border-gray-200 rounded-l-lg hover:bg-gray-100 focus:z-10 focus:ring-2 focus:ring-blue-700 focus:text-blue-700 dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-blue-500 dark:focus:text-white">
    Previous
  </a>
  <a @click.prevent="setStep(1)" href="#" class="px-4 py-2 text-sm font-medium text-gray-900 bg-white border border-gray-200 rounded-r-md hover:bg-gray-100 hover:text-blue-700 focus:z-10 focus:ring-2 focus:ring-blue-700 focus:text-blue-700 dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-blue-500 dark:focus:text-white">
    Next
  </a>
</div>

 </div>
</template>
<script>
/* eslint-disable */
import ChooseFormat from '@/components/Steps/ChooseFormat.vue'
import SelectDimensions from '@/components/Steps/SelectDimensions.vue'
import FabricComponent from '@/components/banner/FabricComponent2.vue'
import CustomizeBanner from '@/components/Steps/CustomizeBanner.vue'
export default{
  components:{
    ChooseFormat,
    SelectDimensions,
    FabricComponent,
    CustomizeBanner
  },
  methods: {
    setStep(value) {
      let result = this.validateSteps(value)
      if(!result.isValidated) {
        alert(result.message)
        return
      }
      let banner = this.$store.state.core.banner
      banner.step = banner.step + value 
      this.$store.commit("banner",banner)
    },
    validateSteps(value) {
      console.log(value)
      let isValidated = true;
      let message = ''
      let banner = this.$store.state.core.banner
      let currentStep = banner.step
      if(currentStep == 1) {
        if(!banner.format){
          message = 'Please select a format'
          isValidated =  false
        }
      }
      if(currentStep == 2) {
        if(!banner.dimensions){
          message = 'Please select banner dimension'
          isValidated =  false
        }
      }
      return { isValidated,message }
    }
  }
}
</script>
